* {
  font-family: Arial, Helvetica, sans-serif; }

main {
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center; }

.form {
  width: 80%;
  max-width: 700px;
  display: flex;
  flex-direction: column; }

.input {
  position: relative;
  width: 50%;
  padding-top: 3.5em; }
  .input:first-child {
    padding-top: 0; }

input {
  width: 100%;
  font-size: 1.25em;
  padding: 0.5em;
  border-radius: 0.25em;
  border-color: coral; }
  input:focus + label, input:not(:placeholder-shown) + label {
    transform: translateY(-4.25em); }
    input:focus + label:after, input:not(:placeholder-shown) + label:after {
      opacity: 1; }
  input:not(:placeholder-shown):invalid:not(:focus) {
    background: red; }

label {
  transform: translateY(-1.4em);
  transition: transform 0.4s;
  display: flex;
  background-size: 0; }
  label:before {
    content: '';
    background-image: inherit;
    background-repeat: no-repeat;
    background-position: center;
    position: absolute;
    top: 50%;
    left: 0.46667em;
    font-size: 1.4em;
    width: 1.4em;
    height: 1.4em;
    transform: translateY(-50%); }
  label:after {
    content: attr(data-text);
    position: absolute;
    top: 50%;
    left: 2.33333em;
    font-size: 1.4em;
    transform: translateY(-50%);
    opacity: 0.5;
    transition: opacity 0.4s; }

.submit {
  display: flex;
  justify-content: center;
  width: 25%;
  font-size: 1.25em;
  margin-top: 2em;
  padding: 0.5em;
  border: solid coral;
  border-radius: 0.25em;
  cursor: pointer; }
